<template>
    <page-view class="role-info">
        <div class="title">
            <span>女武神</span>
            <span> / </span>
            <span>VALKYRIES</span>
        </div>
        <div class="role-box" v-for="(item, index) of role" :key="index">
            <p>{{ item.ext[0].value }}</p>
            <div class="role-arror">
                <div
                    v-for="(item1, indexs) of roleInfo.filter((item2) => {
                        return item2.type == item.ext[2].value;
                    })"
                    :key="indexs"
                    class="role-btn"
                    @click="getRoleDetail(item1.title, item.ext[0].value)"
                >
                    <div class="imgbox">
                        <img v-lazy="item1.ext[4].value[0].url" />
                    </div>
                </div>
            </div>
        </div>

        <page-footer />
    </page-view>
</template>

<script>
import Api from "@/utils/api";
import { ref, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
export default {
    setup() {
        const router = useRouter();
        const info = reactive({
            roleInfo: {},
            role: {},
        });

        const getRoleInfo = async () => {
            info.roleInfo = await Api.getInfo.getInfos({
                pageSize: 200,
                pageNum: 1,
                channelId: 181,
            });
            info.role = await Api.getInfo.getInfos({
                pageSize: 200,
                pageNum: 1,
                channelId: 183,
            });
            info.roleInfo = info.roleInfo.list;
            info.role = info.role.list;

            // info.role.forEach((items, indexs) => {
            //     let a = info.roleInfo.filter((item) => {
            //         return item.type == info.role[indexs].ext[2].value;
            //     });
            //     console.log(a);
            // });

            // console.log(info.roleInfo);
            // console.log(info.role);
            // console.log(a);
        };
        getRoleInfo();

        const getRoleDetail = (title, roleName) => {
            router.push({
                name: "roleDetail",
                query: {
                    title: title,
                    roleName: roleName,
                },
            });
        };
        return { ...toRefs(info), getRoleDetail };
    },
};
</script>

<style lang="scss" scoped>
.role-info {
    padding-top: 1rem;
    background: url("https://www.bh3.com/_nuxt/img/valkyriesbg.adae43f.jpg") top
        no-repeat;
    background-size: 100%;
    .title {
        font-size: 0.6rem;
        font-weight: bold;
        color: #96dbff;
        text-align: center;
        line-height: 1;
        padding: 0.2rem 0;
        span {
            &:first-child {
                color: white;
            }
            &:last-child {
                display: inline-block;
                transform: skew(-11deg);
                font-size: 0.4rem;
                margin-left: 0.01rem;
            }
        }
    }
    .role-box {
        padding: 0 15px;
        margin-bottom: 5px;
        p {
            color: white;
            font-size: 0.3rem;
            font-weight: bold;
            transform: skew(-11deg);
        }
        .role-arror {
            display: flex;
            flex-wrap: wrap;
            .role-btn {
                width: 1.35rem;
                height: 1.7rem;
                transform: skew(-14.5deg);
                border-radius: 0.2rem;
                padding-top: 4px;
                margin: 0 0.1rem 0.1rem 0.24rem;
                font-weight: bold;
                background-color: white;
                color: #000;
                text-align: center;
                .imgbox {
                    margin-left: -0.17rem;
                    text-align: right;
                    img {
                        display: block;
                        width: 1.68rem;
                        position: relative;
                        z-index: 1;
                        transform: skew(14.5deg);
                    }
                }
            }
        }
    }
}
</style>